import styled from "styled-components";

import domtoimage from "dom-to-image";
import { saveAs } from "file-saver";
import { CSVLink } from "react-csv";
import ProgressIndicator from "Components/ProgressIndicator";

const TableHead = styled.div`
  display: flex;
  width: 100%;
  min-height: 32px;
  justify-content: space-between;
  align-items: center;
  position: sticky;
  top: 0;
  background-color: white;
  padding: 20px 40px;
  box-sizing: border-box;
  font-size: 15px;
  // FIXME: 이게 최선은 아닐듯
  border-top-left-radius: 16px;
  border-top-right-radius: 16px;
  box-shadow: 0 4px 4px -4px rgba(0, 0, 0, 0.2);
`;

const TableSection = styled.div`
  position: relative;
  padding: 30px 30px;
  overflow-x: scroll;
  height: 60vmin;
  width: calc(100% - 30px);
  box-sizing: border-box;
`;

const Table = styled.div`
  display: grid;
  position: absolute;
  width: 100%;
  // background-color: white;
  text-align: center;
  align-items: center;
  font-size: 14px;
  color: gray;
  border-top: 1px solid lightgrey;
  border-left: 1px solid lightgrey;
  grid-template-columns: repeat(44, 1fr);
`;

const CheckedBadge = styled.div`
  font-size: 13px;
  background-color: #e9fbf7;
  border-radius: 14px;
  padding: 8px 15px;
  font-weight: 800;
  color: #98d3c5;
`;

const Left = styled.div`
  align-items: center;
  display: flex;
  gap: 20px;
`;

const Right = styled.div`
  align-items: center;
  display: flex;
  gap: 20px;
`;

const Button = styled.button`
  font-size: 13px;
  border-radius: 14px;
  border: none;
  padding: 8px 15px;
  font-weight: 800;
  cursor: pointer;
`;

const RefreshButton = styled(Button)`
  background-color: #fdf5e7;
  color: #e6cc9c;
`;

const ShareButton = styled(Button)`
  background-color: #fdf5e7;
  color: #e6cc9c;
  z-index: 10;

  transition: all 0.3s;
  &:hover {
    transform: scale(1.1);
  }
`;

const Toggle = styled.input`
  -webkit-appearance: none;
  position: relative;
  width: 50px;
  height: 25px;
  background-color: #fafafa;
  border-radius: 50px;
  outline: none;
  box-shadow: 0 1px 8px rgba(0, 0, 0, 0.1);
  transition: background 0.9s;

  &:before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    height: 25px;
    width: 25px;
    border-radius: 50px;
    transition: all 0.5s;
    background-color: #dddddd;
  }

  &:checked {
    background-color: #e9fbf7;
    transition: background 0.9s;
  }

  &:checked:before {
    transform: translate(100%);
    transition: all 0.5s;
    background-color: #98d3c5;
  }
`;

const Side = styled.div`
  display: flex;
  margin-left: -30px;
  gap: 5px;
`;

// FIXME: 개선하기. 시각적으로 숨기지 말고 Dom에서 없애기
const PngButton = styled(Button)`
  background-color: #fdd656;
  color: #fafafa;
  transition: transform 0.5s ease;
  transform: ${(props) => (props.openShare ? "translateX(25px)" : "translateX(-65px)")};
`;

const XslxButton = styled(Button)`
  background-color: #fdd656;
  color: #fafafa;
  transition: transform 0.5s ease;
  transform: ${(props) => (props.openShare ? "translateX(30px)" : "translateX(-140px)")};
`;

const GridBorder = styled.div`
  padding: 5px;
  border-bottom: 1px solid lightgrey;
  border-right: 1px solid lightgrey;
  min-height: 14px;
`;

// 월화수목금토일
const TableDay = styled(GridBorder)`
  grid-row: 1;
  grid-column: span 6;
  text-align: center;
  border-top: ${(props) => (props.num > 2 ? "1px solid lightgrey" : null)};
`;

// A~F 교시
const TableTime = styled(GridBorder)`
  grid-row: 2/3;
  text-align: center;
  width: 90px;
`;

// 구분
const TableCategory = styled(GridBorder)`
  grid-row: 1/2;
  grid-column: 1/3;
`;

const LectureTypeHead = styled(GridBorder)`
  grid-row: 2/3;
  grid-column: 1/2;
`;

const TeacherNameHead = styled(GridBorder)`
  grid-row: 2/3;
  grid-column: 2/3;
`;

const LectureType = styled(GridBorder)`
  grid-row: ${(props) => props.index + 3};
  grid-column: 1/2;
  width: 70px;
`;

const TeacherName = styled(GridBorder)`
  grid-row: ${(props) => props.index + 3};
  grid-column: 2/3;
  width: 90px;
`;

const TeacherStudent = styled(GridBorder)`
  grid-column-start: ${(props) => props.startIndex - 1};
  grid-column-end: ${(props) => props.startIndex};
  grid-row: ${(props) => props.index + 3};
`;

// 테이블 아래의 미편성 구성원 섹션
const BottomSection = styled.div`
  display: flex;
  height: fit-content;
  padding: 30px 30px;
`;

// BottomSection의 좌,우 섹션
// 왼쪽: 미편성강사, 오른쪽: 미편성학생
const HalfSection = styled.div`
  flex-basis: 50%;
  padding: 10px 10px;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 5px;
`;

const SectionTitle = styled.div`
  font-size: 15px;
  font-weight: 700;
  margin-bottom: 20px;
  grid-column: span 4;
`;

const Card = styled.div`
  padding: 10 10px;
  font-size: 12px;
  font-color: gray;
  display: flex;
  width: 100%;
  height: 60px;
  background-color: #fafafa;
  // border: 1px solid lightgrey;
  border-radius: 8px;
  box-shadow: 0 7px 30px -10px rgba(150, 170, 180, 0.5);
  &:hover {
    transition: transform ease 0.4s;
    transform: scale(1.04);
  }
`;

const CardLeft = styled.div`
  flex-basis: 40%;
  padding: 10px;
  text-align: center;
`;

const CardRight = styled.div`
  flex-basis: 60%;
  padding: 10px;
`;

const CardName = styled.div`
  font-size: 13px;
  font-weight: 700;
`;

const CardTeacherSubject = styled.div`
  font-size: 12px;
  color: gray;
  margin-top: 5px;
`;

const StyledCSVLink = styled(CSVLink)`
  text-decoration: none;
  color: white;
`;

const SideButtons = ({ openShare, CSVData, CSVHeaders }) => {
  return (
    <Side>
      <PngButton openShare={openShare} onClick={onDownloadBtn}>
        <i className="fas fa-file-image"></i> png{" "}
      </PngButton>
      <XslxButton openShare={openShare}>
        <StyledCSVLink data={CSVData} headers={CSVHeaders} filename={"table.csv"}>
          <i className="fas fa-file-excel"></i> xlsx
        </StyledCSVLink>
      </XslxButton>
    </Side>
  );
};

const onDownloadBtn = () => {
  domtoimage
    .toBlob(document.querySelector(".table"), { bgcolor: "white" })
    .then((blob) => {
      // TODO: 학생/강사명 받아서 파일 이름 바꿔주기 ex) 김가현.png
      saveAs(blob, "table.png");
    })
    .catch((e) => {
      console.log(e);
    });
};

const TablePresenter = ({
  loading,
  openShare,
  setOpenShare,
  table,
  active,
  setActive,
  handleRefresh,
  CSVHeaders,
  CSVData,
}) => {
  const days = ["월", "화", "수", "목", "금", "토", "일"];
  const times = ["A", "B", "C", "D", "E", "F"];

  const renderTimes = () => {
    const result = [];
    for (let i = 0; i < days.length; i++) {
      times.map((time, index) => result.push(<TableTime>{time}</TableTime>));
    }
    return result;
  };

  return (
    <>
      <TableHead>
        <Left>
          {active ? (
            <CheckedBadge>
              {" "}
              <i className="fas fa-check"></i> &nbsp;선택된 시간표{" "}
            </CheckedBadge>
          ) : null}
          <ShareButton
            onClick={() => {
              setOpenShare(!openShare);
            }}
          >
            <i className="fas fa-file-download"></i>&nbsp; 내보내기
          </ShareButton>
          <SideButtons openShare={openShare} CSVHeaders={CSVHeaders} CSVData={CSVData} />
        </Left>
        <Right>
          <Toggle
            className="toggle"
            onChange={(e) => {
              setActive(!active);
            }}
            type="checkbox"
            checked={active}
          ></Toggle>
          <RefreshButton onClick={handleRefresh}>
            <i className="fas fa-redo-alt"></i> &nbsp;새 시간표 출력
          </RefreshButton>
        </Right>
      </TableHead>

      <TableSection>
        {loading ? (
          <ProgressIndicator />
        ) : (
          <Table className="table">
            <TableCategory> 구분 </TableCategory>
            <LectureTypeHead> 유형 </LectureTypeHead>
            <TeacherNameHead> 강사 </TeacherNameHead>
            {days.map((day, index) => (
              <TableDay num={index}> {day} </TableDay>
            ))}
            {renderTimes()}
            {table &&
              table.personal &&
              table.personal.length > 0 &&
              table.personal.map((row, index) => (
                <>
                  <LectureType index={index}> 개인수업 </LectureType>
                  <TeacherName index={index}>
                    {" "}
                    {row.teacher.name} ({row.teacher.subject})
                  </TeacherName>
                  {row.lecture &&
                    row.lecture.length > 0 &&
                    row.lecture.map((day, dIndex) => {
                      return times.map((time, tIndex) => (
                        <TeacherStudent index={index} startIndex={tIndex + dIndex * times.length + 4}>
                          {day[time] && day[time].student && day[time].student.name ? day[time].student.name : null}
                        </TeacherStudent>
                      ));
                    })}
                </>
              ))}
          </Table>
        )}
      </TableSection>

      <BottomSection>
        <HalfSection>
          <SectionTitle> 미편성 강사 </SectionTitle>
          {table &&
            table.unorganized.teachers.map((teacher, index) => (
              <Card>
                <CardLeft>
                  <CardName>{teacher.name} </CardName>
                  <CardTeacherSubject>{teacher.subject}</CardTeacherSubject>
                </CardLeft>
                <CardRight>
                  {teacher.officeHours &&
                    teacher.officeHours.map((oHour, index) => (
                      <>
                        {oHour.day}({oHour.times})
                      </>
                    ))}
                </CardRight>
              </Card>
            ))}
        </HalfSection>

        <HalfSection>
          <SectionTitle> 미편성 학생 </SectionTitle>
          {table &&
            table.unorganized.students.map((student, index) => (
              <Card>
                <CardLeft>
                  <CardName> {student.name} </CardName>
                </CardLeft>
                <CardRight>
                  {student.subjects &&
                    student.subjects.map((subject, index) => (
                      <>
                        {" "}
                        {subject.name} ({subject.hours}){" "}
                      </>
                    ))}
                </CardRight>
              </Card>
            ))}
        </HalfSection>
      </BottomSection>
    </>
  );
};

export default TablePresenter;
