import DailyTablePresenter from "./DailyTablePresenter";
import React, { useState, useEffect, useRef } from "react";
import { tableAPI } from "api";

const DailyTableContainer = () => {
  const [dayIndex, setDayIndex] = useState(0);
  const [openShare, setOpenShare] = useState(false);
  const [table, setTable] = useState([]);
  const [active, setActive] = useState(false);
  const [dailyTable, setDailyTable] = useState([]);
  const [loading, setLoading] = useState(false);
  const [CSVHeaders, setCSVHeaders] = useState([]);
  const [CSVData, setCSVData] = useState([]);

  const getTable = async () => {
    try {
      setLoading(true);
      const {
        data: { table: tableData },
      } = await tableAPI.readTable();
      setTable(tableData.personal);
      setActive(tableData.active);
    } catch {
    } finally {
      setLoading(false);
    }
  };

  const getDailyTable = () => {
    const dt = [];
    table.forEach((teacher) => {
      dt.push({ teacher: teacher.teacher, subject: teacher.subject, lecture: teacher.lecture[dayIndex] });
    });

    setDailyTable(dt);
  };

  const getCSVHeaders = () => {
    const csvHeaders = [
      { label: "유형", key: "type" },
      { label: "강사", key: "teacher" },
      { label: "A", key: "lecture.A" },
      { label: "B", key: "lecture.B" },
      { label: "C", key: "lecture.C" },
      { label: "D", key: "lecture.D" },
      { label: "E", key: "lecture.E" },
      { label: "F", key: "lecture.F" },
    ];

    setCSVHeaders(csvHeaders);
  };

  const getCSVData = () => {
    const data = dailyTable.map((t) => {
      const lecture = {};
      for (const day in t.lecture) {
        const cell = t.lecture[day];
        lecture[day] = cell && cell.student ? cell.student.name : "";
      }
      return {
        type: "개인수업",
        teacher: `${t.teacher.name} (${t.subject})`,
        lecture,
      };
    });

    setCSVData(data);
  };

  const handleChangeDayIndex = ({ target: { selectedOptions } }) => {
    const dayIdx = selectedOptions[0].value;
    setDayIndex(dayIdx);
  };

  useEffect(() => {
    getTable();
    getCSVHeaders();
  }, []);

  useEffect(() => {
    getDailyTable();
  }, [table]);

  useEffect(() => {
    getDailyTable();
  }, [dayIndex]);

  useEffect(() => {
    getCSVData();
  }, [dailyTable]);

  return (
    <DailyTablePresenter
      openShare={openShare}
      setOpenShare={setOpenShare}
      dayIndex={dayIndex}
      handleChangeDayIndex={handleChangeDayIndex}
      active={active}
      dailyTable={dailyTable}
      loading={loading}
      CSVHeaders={CSVHeaders}
      CSVData={CSVData}
    />
  );
};

export default DailyTableContainer;
