import React, { useState, useRef, useEffect } from "react";
import PersonalTablePresenter from "./PersonalTablePresenter";
import { studentAPI, teacherAPI, tableAPI } from "api";

const days = ["월", "화", "수", "목", "금", "토", "일"];
const times = ["A", "B", "C", "D", "E", "F"];

const PersonalTableContainer = () => {
  const [loading, setLoading] = useState(false);
  const [dayIndex, setDayIndex] = useState(0); // 월, 화, 수, 목, 금, 토, 일
  const [category, setCategory] = useState(""); // student, teacher
  const [person, setPerson] = useState(""); // id
  const [table, setTable] = useState();
  const [active, setActive] = useState();
  const [personalTable, setPersonalTable] = useState();
  const [students, setStudents] = useState([]);
  const [teachers, setTeachers] = useState([]);
  const [openShare, setOpenShare] = useState(false);
  const [CSVHeaders, setCSVHeaders] = useState([]);
  const [CSVData, setCSVData] = useState([]);

  const getDailyData = async () => {
    setLoading(true);
    try {
      const {
        data: { table: tableData },
      } = await tableAPI.readTable();
      const {
        data: { students: studentsData },
      } = await studentAPI.readAllStudents();
      const {
        data: { teachers: teachersData },
      } = await teacherAPI.readAllTeachers();

      setTable(tableData.personal);
      setActive(tableData.active);
      setStudents(studentsData);
      setTeachers(teachersData);

      // 자바스크립트 Date.getDay() 에서 일요일:0, 월요일:1, 토요일:6
      let dayIndex = new Date().getDay() - 1;
      if (dayIndex < 0) dayIndex = days.length - 1;
      setDayIndex(dayIndex);
    } catch {
    } finally {
      setLoading(false);
    }
  };

  const getPersonalTable = () => {
    if (category === "students") {
      const pt = [{}, {}, {}, {}, {}, {}, {}];
      table &&
        table.length > 0 &&
        table.forEach((teacher) => {
          // 16
          teacher.lecture.forEach((day, dayIndex) => {
            // 7
            for (let [time, value] of Object.entries(day)) {
              // 6
              if (value.student && value.student._id === person) {
                pt[dayIndex][time] = teacher.teacher;
              }
            }
          });
        });
      setPersonalTable(pt);
    } else {
      const pt = [];
      const index = table && table.length > 0 && table.findIndex((t) => String(t.teacher._id) === person);
      table &&
        table.length > 0 &&
        table[index].lecture &&
        table[index].lecture.forEach((day) => {
          // 7
          const obj = new Object();
          for (let [time, value] of Object.entries(day)) {
            if (value.student) obj[time] = value.student;
          }
          pt.push(obj);
        });
      setPersonalTable(pt);
    }
  };

  const handleChangeCategory = ({ target: { selectedOptions } }) => {
    const category = selectedOptions[0].value;
    setPerson("");
    setCategory(category);
    setPersonalTable(null);
  };

  const handleChangePerson = ({ target: { selectedOptions } }) => {
    const person = selectedOptions[0].value;
    setPerson(person);
  };

  const getCSVData = () => {
    const engDays = ["mon", "tue", "wed", "thu", "fri", "sat", "sun"];
    const data = [];
    times.forEach((time, timeIdx) => {
      const row = { time, lecture: { mon: {}, tue: {}, wed: {}, thu: {}, fri: {}, sat: {}, sun: {} } };
      personalTable &&
        personalTable.forEach((day, dayIdx) => {
          row.lecture[engDays[dayIdx]] = day[time] ? day[time].name : "";
        });
      console.log(row);
      data.push(row);
    });
    console.log(data);
    setCSVData(data);
  };

  const getCSVHeaders = () => {
    const csvHeaders = [
      { label: "구분", key: "time" },
      { label: "월", key: "lecture.mon" },
      { label: "화", key: "lecture.tue" },
      { label: "수", key: "lecture.wed" },
      { label: "목", key: "lecture.thu" },
      { label: "금", key: "lecture.fri" },
      { label: "토", key: "lecture.sat" },
      { label: "일", key: "lecture.sun" },
    ];

    setCSVHeaders(csvHeaders);
  };

  useEffect(() => {
    getDailyData();
    getCSVHeaders();
  }, []);

  useEffect(() => {
    if (category && person) {
      getPersonalTable();
    }
  }, [category, person]);

  useEffect(() => {
    getCSVData();
  }, [personalTable]);

  return (
    <PersonalTablePresenter
      openShare={openShare}
      setOpenShare={setOpenShare}
      dayIndex={dayIndex}
      category={category}
      person={person}
      students={students}
      teachers={teachers}
      active={active}
      personalTable={personalTable}
      handleChangeCategory={handleChangeCategory}
      handleChangePerson={handleChangePerson}
      CSVHeaders={CSVHeaders}
      CSVData={CSVData}
    />
  );
};

export default PersonalTableContainer;
