import React, { useState, useEffect, useRef } from "react";
import { studentAPI, teacherAPI } from "api";
import StudentModifyPresenter from "./StudentModifyPresenter";
import HandleConfirm from "Components/HandleConfirm";

const StudentModifyContainer = ({ modalClose, studentData }) => {
  const subjectsOrder = ["국어", "영어", "수학", "사탐", "과탐"];
  const days = ["월", "화", "수", "목", "금", "토", "일"];
  const times = ["A", "B", "C", "D", "E", "F"];

  const [student, setStudent] = useState(null);
  const [teachers, setTeachers] = useState(new Object());
  const [grade, setGrade] = useState(0);
  const [category, setCategory] = useState(0);
  const [subjects, setSubjects] = useState([]);
  const [officeHours, setOfficeHours] = useState([]);
  const [isModify, setIsModify] = useState(false);

  useEffect(() => {
    setStudent(studentData);
    studentData && setSubjects(studentData.subjects);
    studentData && console.log(studentData.subjects);
    studentData && setOfficeHours(studentData.officeHours);
    studentData && setIsModify(true);
    studentData && setGrade(studentData.grade);
    studentData && setCategory(studentData.category);
    getTeachersBySubjectsData();
  }, []);

  const inputName = useRef();
  const inputSchool = useRef();
  const inputPhone = useRef();
  const inputParents = useRef();

  // FIXME: 데이터를 어떻게 가지고 있는게 좋을지 고민
  const getTeachersBySubjectsData = () => {
    const teachersBySubjects = new Object();

    subjectsOrder.map(async (subject) => {
      try {
        const {
          data: { teachers: teachersData },
        } = await teacherAPI.readTeachersBySubject(subject);
        teachersBySubjects[subject] = teachersData;
      } catch {}
      setTeachers(JSON.parse(JSON.stringify(teachersBySubjects)));
    });
  };

  const handleSubmit = () => {
    const newStudent = {
      name: inputName.current.value,
      school: inputSchool.current.value,
      phone: inputPhone.current.value,
      parents: inputParents.current.value,
      category,
      grade,
      subjects,
      officeHours,
    };

    if (isModify) newStudent._id = student._id;

    HandleConfirm("저장하시겠습니까?", () => (isModify ? updateStudent(newStudent) : createStudent(newStudent)), null);
  };

  const createStudent = async (student) => {
    try {
      const {
        data: { result },
      } = await studentAPI.createStudent(student);
      if (result) {
        alert("등록 성공");
        modalClose();
      }
    } catch {}
  };

  const updateStudent = async (student) => {
    try {
      const result = await studentAPI.updateStudent(student);
      if (result) {
        alert("수정 성공");
        modalClose();
      }
    } catch {}
  };

  const handleChangeGrade = ({ target: { selectedOptions } }) => {
    const grade = selectedOptions[0].value;
    console.log(`grade: ${grade}`);
    setGrade(grade);
  };

  const handleChangeCategory = ({ target: { selectedOptions } }) => {
    const category = selectedOptions[0].value;
    console.log(`category: ${category}`);
    setCategory(category);
  };

  const handleChangeSubjects = ({ target }) => {
    const { value, checked } = target;
    const index = subjects.findIndex((subject) => subject.name === value);
    if (checked && index < 0) subjects.push({ name: value, hours: 0 });
    if (!checked && index > -1) subjects.splice(index, 1);

    subjects.sort((a, b) => (subjectsOrder.indexOf(a.name) >= subjectsOrder.indexOf(b.name) ? 1 : -1));
    setSubjects(JSON.parse(JSON.stringify(subjects)));
  };

  const handleChangeSubjectHours = ({ currentTarget: target }) => {
    const [sub, calc] = target.value.split(" ");
    const index = subjects.findIndex((subject) => subject.name === sub);
    if (index > -1 && calc === "plus" && subjects[index].hours < 6) subjects[index].hours += 1;
    if (index > -1 && calc === "minus" && subjects[index].hours > 0) subjects[index].hours -= 1;

    setSubjects(JSON.parse(JSON.stringify(subjects)));
  };

  const handleChangePreferredTeacher = ({ target: { selectedOptions } }) => {
    const [sub, teacherID] = selectedOptions[0].value.split(" ");
    console.log(sub, teacherID);
    const index = subjects.findIndex((subject) => subject.name === sub);
    if (index > -1) subjects[index].teacher = teacherID;

    setSubjects(JSON.parse(JSON.stringify(subjects)));
  };

  const handleChangeOfficeHours = ({ currentTarget: target }) => {
    const { value, checked } = target;
    const index = officeHours.findIndex((officeHour) => officeHour.day === value);
    if (checked && index < 0) officeHours.push({ day: value, times: [] });
    if (!checked && index > -1) officeHours.splice(index, 1);
    officeHours.sort((a, b) => (days.indexOf(a.day) >= days.indexOf(b.day) ? 1 : -1));
    setOfficeHours(JSON.parse(JSON.stringify(officeHours)));
  };

  const handleChangeTimes = ({ currentTarget: target }) => {
    const { value, checked } = target;
    const [day, time] = value.split("");
    const dayIndex = officeHours.findIndex((officeHour) => officeHour.day === day);
    if (dayIndex > -1) {
      const timeIndex = officeHours[dayIndex].times.findIndex((t) => t === time);
      if (checked && timeIndex < 0) officeHours[dayIndex].times.push(time);
      if (!checked && timeIndex > -1) officeHours[dayIndex].times.splice(timeIndex, 1);
      officeHours[dayIndex].times.sort((a, b) => (times.indexOf(a) >= times.indexOf(b) ? 1 : -1));
    }
    setOfficeHours(JSON.parse(JSON.stringify(officeHours)));
  };

  return (
    <StudentModifyPresenter
      handleChangeGrade={handleChangeGrade}
      handleChangeCategory={handleChangeCategory}
      handleChangeSubjects={handleChangeSubjects}
      handleChangeSubjectHours={handleChangeSubjectHours}
      handleChangePreferredTeacher={handleChangePreferredTeacher}
      handleChangeOfficeHours={handleChangeOfficeHours}
      handleChangeTimes={handleChangeTimes}
      handleSubmit={handleSubmit}
      student={student}
      subjectsOrder={subjectsOrder}
      days={days}
      times={times}
      category={category}
      grade={grade}
      subjects={subjects}
      officeHours={officeHours}
      inputName={inputName}
      inputSchool={inputSchool}
      inputPhone={inputPhone}
      inputParents={inputParents}
      teachers={teachers}
      // isModify={isModify}
    />
  );
};

export default StudentModifyContainer;
